/**
 * SweetAlert2 Helper Utilities - Nyan Cat Style Mushroom/Mycelium Theme
 * ======================================================================
 * Provides themed alert modals with animated mushroom/mycelium styling
 * inspired by Nyan Cat but with psychedelic fungal vibes.
 *
 * Features:
 * - Rainbow gradient animated borders
 * - Floating spore particles
 * - Breathing glow effects
 * - Mycelium network backdrop
 * - Animate.css integration
 */

import Swal from 'sweetalert2';
import 'animate.css';

// Inject custom CSS for mushroom-themed animations
const injectMushroomStyles = () => {
  const styleId = 'swal-mushroom-nyan-styles';
  if (document.getElementById(styleId)) return;

  const style = document.createElement('style');
  style.id = styleId;
  style.textContent = `
    /* Subtle rainbow gradient animation for border */
    @keyframes mushroomRainbow {
      0% { background-position: 0% 50%; }
      50% { background-position: 100% 50%; }
      100% { background-position: 0% 50%; }
    }

    /* Swaying mushroom animation - gentle wind effect */
    @keyframes swayMushroom {
      0%, 100% { transform: rotate(-5deg) translateY(0); }
      25% { transform: rotate(3deg) translateY(-3px); }
      50% { transform: rotate(-3deg) translateY(2px); }
      75% { transform: rotate(5deg) translateY(-2px); }
    }

    /* Breathing glow effect - more subtle */
    @keyframes breathingGlow {
      0%, 100% { box-shadow: 0 0 20px rgba(124, 58, 237, 0.3), 0 0 40px rgba(236, 72, 153, 0.15); }
      50% { box-shadow: 0 0 30px rgba(124, 58, 237, 0.4), 0 0 60px rgba(236, 72, 153, 0.2); }
    }

    /* Title glow - subtle */
    @keyframes titleGlow {
      0%, 100% { text-shadow: 0 0 10px rgba(251, 191, 36, 0.4), 0 2px 4px rgba(0, 0, 0, 0.5); }
      50% { text-shadow: 0 0 20px rgba(251, 191, 36, 0.6), 0 2px 4px rgba(0, 0, 0, 0.5); }
    }

    /* Button breathing - subtle */
    @keyframes buttonBreathing {
      0%, 100% { transform: scale(1); }
      50% { transform: scale(1.02); }
    }

    /* Glassmorphism popup - toned down, more readable */
    .swal-mushroom-popup {
      background: linear-gradient(135deg, rgba(20, 20, 35, 0.97) 0%, rgba(35, 25, 55, 0.97) 50%, rgba(20, 20, 35, 0.97) 100%) !important;
      backdrop-filter: blur(16px) !important;
      -webkit-backdrop-filter: blur(16px) !important;
      border: 2px solid rgba(124, 58, 237, 0.5) !important;
      border-radius: 20px !important;
      position: relative !important;
      overflow: visible !important;
      box-shadow: 0 0 20px rgba(124, 58, 237, 0.3), 0 0 40px rgba(236, 72, 153, 0.15);
    }

    /* Only apply breathing glow when popup is visible and not animating out */
    .swal2-popup.swal-mushroom-popup:not(.animate__zoomOut) {
      animation: breathingGlow 5s ease-in-out infinite;
    }

    .swal-mushroom-popup::before {
      content: '';
      position: absolute;
      inset: -2px;
      border-radius: 22px;
      background: linear-gradient(135deg, #7c3aed, #ec4899, #a78bfa, #7c3aed);
      background-size: 300% 300%;
      animation: mushroomRainbow 10s ease infinite;
      z-index: -1;
      opacity: 0.7;
    }

    /* Scattered swaying mushrooms in backdrop */
    .swal-mushroom-backdrop-shroom {
      position: fixed;
      font-size: 2rem;
      pointer-events: none;
      animation: swayMushroom 3s ease-in-out infinite;
      opacity: 0.6;
      filter: drop-shadow(0 0 8px rgba(124, 58, 237, 0.5));
    }

    /* Glowing title - subtle */
    .swal-mushroom-title {
      animation: titleGlow 4s ease-in-out infinite !important;
      font-weight: 700 !important;
      letter-spacing: 0.02em !important;
      color: #fbbf24 !important;
    }

    /* Styled content - better readability */
    .swal-mushroom-content {
      color: #e2e8f0 !important;
      line-height: 1.7 !important;
      font-size: 1rem !important;
    }

    /* Button with subtle breathing */
    .swal-mushroom-button {
      background: linear-gradient(135deg, #7c3aed, #ec4899) !important;
      border: none !important;
      border-radius: 12px !important;
      font-weight: 600 !important;
      padding: 14px 32px !important;
      animation: buttonBreathing 3s ease-in-out infinite !important;
      transition: all 0.3s ease !important;
      font-size: 1rem !important;
      box-shadow: 0 4px 15px rgba(124, 58, 237, 0.4) !important;
    }

    .swal-mushroom-button:hover {
      transform: scale(1.05) translateY(-2px) !important;
      box-shadow: 0 8px 30px rgba(236, 72, 153, 0.5), 0 0 20px rgba(124, 58, 237, 0.3) !important;
    }

    /* CTA link styling in footer - prominent GitHub link */
    .swal2-footer {
      border-top: 1px solid rgba(124, 58, 237, 0.3) !important;
      margin-top: 16px !important;
      padding-top: 16px !important;
    }

    .swal2-footer a.github-cta-link {
      display: inline-block;
      background: linear-gradient(135deg, #a78bfa, #7c3aed) !important;
      color: #ffffff !important;
      padding: 12px 24px !important;
      border-radius: 10px !important;
      font-weight: 600 !important;
      font-size: 1rem !important;
      text-decoration: none !important;
      border: none !important;
      transition: all 0.3s ease !important;
      box-shadow: 0 4px 15px rgba(124, 58, 237, 0.4) !important;
    }

    .swal2-footer a.github-cta-link:hover {
      transform: scale(1.05) !important;
      box-shadow: 0 6px 25px rgba(168, 139, 250, 0.6) !important;
      background: linear-gradient(135deg, #c4b5fd, #a78bfa) !important;
    }

    .swal-mushroom-content a,
    .swal2-footer a:not(.github-cta-link) {
      color: #a78bfa !important;
      text-decoration: none !important;
      border-bottom: 1px dotted #a78bfa !important;
      transition: all 0.3s ease !important;
      font-weight: 500 !important;
    }

    .swal-mushroom-content a:hover,
    .swal2-footer a:not(.github-cta-link):hover {
      color: #ec4899 !important;
      border-bottom-color: #ec4899 !important;
    }
  `;
  document.head.appendChild(style);
};

// Initialize styles on module load
injectMushroomStyles();

// Create scattered mushroom backdrop HTML
const createMushroomBackdrop = () => {
  // Positions for scattered mushrooms swaying in the wind
  const mushrooms = [
    { top: '10%', left: '5%', delay: '0s', size: '2rem' },
    { top: '20%', right: '8%', delay: '0.5s', size: '2.5rem' },
    { top: '35%', left: '12%', delay: '1s', size: '1.8rem' },
    { top: '50%', right: '6%', delay: '0.3s', size: '2.2rem' },
    { top: '65%', left: '8%', delay: '0.7s', size: '2rem' },
    { top: '75%', right: '10%', delay: '1.2s', size: '2.3rem' },
    { top: '85%', left: '15%', delay: '0.2s', size: '1.9rem' },
    { bottom: '10%', right: '12%', delay: '0.8s', size: '2.1rem' },
  ];

  return mushrooms.map((m) => `
    <span class="swal-mushroom-backdrop-shroom" style="
      ${m.top ? `top: ${m.top};` : ''}
      ${m.bottom ? `bottom: ${m.bottom};` : ''}
      ${m.left ? `left: ${m.left};` : ''}
      ${m.right ? `right: ${m.right};` : ''}
      font-size: ${m.size};
      animation-delay: ${m.delay};
    "></span>
  `).join('');
};

// GitHub SVG icon for use in HTML
const githubSvgIcon = `<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="currentColor" style="display:inline-block;vertical-align:middle;margin-right:8px;"><path d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z"/></svg>`;

// Base styling for all alerts - mushroom/mycelium theme
const baseStyles = {
  background: 'linear-gradient(135deg, rgba(20, 20, 35, 0.97) 0%, rgba(35, 25, 55, 0.97) 100%)',
  color: '#e2e8f0',
  confirmButtonColor: '#7c3aed',
  confirmButtonText: 'Return to the Network ',
  customClass: {
    popup: 'swal-mushroom-popup',
    title: 'swal-mushroom-title',
    htmlContainer: 'swal-mushroom-content',
    confirmButton: 'swal-mushroom-button',
  },
  showClass: {
    popup: 'animate__animated animate__zoomIn animate__faster'
  },
  hideClass: {
    popup: 'animate__animated animate__zoomOut animate__faster'
  },
  backdrop: `rgba(0, 0, 15, 0.85)`,
  allowOutsideClick: true,
  allowEscapeKey: true,
  didOpen: () => {
    // Add scattered swaying mushrooms to backdrop
    const backdrop = document.querySelector('.swal2-container');
    if (backdrop && !backdrop.querySelector('.swal-mushroom-backdrop-shroom')) {
      backdrop.insertAdjacentHTML('afterbegin', createMushroomBackdrop());
    }
  },
  willClose: () => {
    // Clean up mushrooms
    const mushrooms = document.querySelectorAll('.swal-mushroom-backdrop-shroom');
    mushrooms.forEach(m => m.remove());
  }
};

/**
 * GitHub-specific alert with prominent CTA button
 */
export const showGitHubAlert = () => {
  return Swal.fire({
    icon: 'info',
    iconColor: '#a78bfa',
    title: 'Psanctuary GitHub Coming Soon! ',
    html: '<p style="margin-bottom: 8px;">The official Psanctuary repository is being prepared.</p>',
    footer: `<p style="color: #9ca3af; font-size: 1.05rem;">Connect with the developer:</p><a href="https://github.com/drasticstatic" target="_blank" rel="noopener noreferrer" class="github-cta-link">Meet the Developer&nbsp;&nbsp;${githubSvgIcon}GitHub/drasticstatic</a>`,
    ...baseStyles,
  });
};

/**
 * Generic coming soon alert with mushroom/mycelium theme
 */
export const showComingSoonAlert = (title, emoji = '') => {
  return Swal.fire({
    title: `${title} ${emoji}`,
    ...baseStyles,
  });
};

/**
 * Discord-specific alert (private servers)
 */
