// Map of alert types to their handlers
export const alertHandlers = {
  discord: showDiscordAlert,
  telegram: showTelegramAlert,
  twitter: showTwitterAlert,
  github: showGitHubAlert,
  youtube: showYouTubeAlert,
  facebook: showFacebookAlert,
  instagram: showInstagramAlert,
};

/**
 * Get the appropriate alert handler for a given card ID
 */
export const getAlertHandler = (cardId) => {
  return alertHandlers[cardId] || (() => showComingSoonAlert('Coming soon'));
};

